@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
  background-image: url('https://i.imgur.com/XBhhbOk.png');
  color: white;
  font-family:'Courier New', Courier, monospace;
  font-size: 18px;
}

.logo{
  width: 20%;
  height: 20%;
}

header{
  width: 100%;
  text-align: center;
}

.menu, .input-div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

select, input, button{
  padding: 10px;
  margin: 10px;
  font-family: 'Press Start 2P', cursive;
}

.filter-container{
  display: flex;
  flex-direction: row;
  font-weight: bold;
  font-size: 20px;
}

.filters{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

th{
  color:black;
  background-color: white;
  border-radius: 8px;
  padding: 5px 5px 5px 5px;
  font-family: 'Press Start 2P', cursive;
  font-size: 10px;
}

td{
  text-align: center;
  border-color: white;
  border-width: 1px;
  border-style: solid;
  font-weight: 700;
  padding:10px;
}
table{
  width: 90%;
  
}
.table{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}